
import saleRepotService from "service/saleRepotService";
import Cell from 'vux/src/components/cell/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'
import querystring from 'vux/src/tools/querystring/index.js'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'


import {toFixed, accAdd, accMul} from 'plugins/calc'

export default {
components: {
Alert,
Cell,
Group,
Divider,
XButton,
Confirm,
LoadMore,
FormPreview,
CellFormPreview
},
data() {
return {
list1: [],
list2: [],
list3: [],
list4: [],
total1: "",
total2: "",
total3: "",
total4: "",
childInfo: "",
listSalesAmt: [],
};
},
filters: {
numberComma
},
methods: {
/*
* 
*/

sendOrder() {
let that = this;
this.$vux.confirm.show({
title: "",
content: "",
confirmText: "",
cancelText: "",
onConfirm() {
saleRepotService.subAmount(querystring.stringify(that.childInfo)).then(data => {
if (data.success) {
that.$vux.alert.show({
title: "",
content: data.message,
onHide() {
that.$router.replace("/Home");
localStorage.removeItem("saleReportInfo");
localStorage.removeItem("HELP_BASIC_INFO");
localStorage.removeItem("HELP_FORM_INFO");
localStorage.removeItem("SALE_FORM_INFO");
}
});
}
}).catch(data => {
if (data.success == false) {
that.$vux.alert.show({
title: "",
content: data.message
});
}
});
}
});
},

info() {
let total1 = 0, // 
total2 = 0, // A
total3 = 0, // B
list4_num = 0, //  
totalNum1 = 0; 


let jsonData = JSON.parse(this.childInfo.jsonData);
let {transDetailUncalc, baseinfoExt = {}} = jsonData;

for (let i = 0; i < transDetailUncalc.length; i++) {
let item = transDetailUncalc[i];
if (item.containerCode === "" && item.transObjCode) {

this.list1.push({
label: item.transObjCode,
value: `${item.qty || 0}/${item.num1 || 0}`
});

list4_num = accAdd(list4_num, Number(item.qty || 0));
totalNum1 = accAdd(totalNum1, Number(item.num1 || 0));

total1 = accAdd(total1, item.amount);
} else if (item.containerCode == "A") {
this.list2.push({
label: item.transObjCode,
value: item.qty
});
total2 = item.amount;
} else if (item.containerCode == "B") {
this.list3.push({
label: item.transObjCode,
value: item.qty
});
total3 = item.amount;
}
}
this.total1 = "" + total1;
this.total2 = "" + total2;
this.total3 = "" + total3;
let num = Number(total2) + Number(total3);
this.total4 = "" + num;
this.list4 = [
{
label: "",
value: `${list4_num}/${totalNum1}`
}
];
this.listSalesAmt = [
{
label: '',
value: `${numberComma(baseinfoExt.double7)}`,
}, {
label: '',
value: `${numberComma(baseinfoExt.double10)}`,
}, {
label: '',
value: `${numberComma(baseinfoExt.double11)}`,
}, {
label: '',
value: `${numberComma(baseinfoExt.double8)}`,
}, {
label: '',
value: `${numberComma(baseinfoExt.double9)}`,
}, {
label: '',
value: baseinfoExt.varchar13,
},
]
}
},
mounted() {
this.childInfo = JSON.parse(
localStorage.getItem("saleReportInfo")
).saleReportRemark;
this.info();
}
};



